@import "~node_modules/typeface-roboto/index.css";
body,
html {
    margin: 0;
    font-family: "Roboto", "Arial", "Helvetica", sans-serif;
}

$sizeChangeVar: 935px;
$laptopChangeVar: 1210px;

.container {
    display: grid;
    grid-template-columns:70% 30%;
    @media screen and (max-width: $laptopChangeVar){
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column;
    }
    .sidebars {
        @media screen and (max-width: $sizeChangeVar){
            width: 100%;
        }
        display: flex;
        justify-content: center;
        align-items: center;
        flex-flow: column;
        .box {
            margin-top: 35px;
            display: inline-grid;
            align-items: center;
            justify-items: center;
            height: 600px;
            grid-template-rows: 25% 75%;
            width: 100%;
            .text_ici {
                display: flex;
                /*width: 50%;*/
                justify-content: center;
                align-items: center;
                .spacing {
                    width: 300px;
                    @media screen and (max-width: $sizeChangeVar) {
                        width: 0px;
                        display: none;
                    }
                    height: 1px;
                }
                .ici {
                    font-weight: bold;
                    font-size: 250px;
                    z-index: 0;
                    color: #00a9d4;
                    user-select: none;
                }
            }
            .rectangle {
                display: grid;
                grid-template-columns: 0% 100% 0%;
                width: 100%;
                @media screen and (min-width: $sizeChangeVar) {
                    width: 800px;
                    grid-template-columns: 20% 30% 50%;
                    border-radius: 30px 0px 0px 0px;
                }
                .arrow {
                    display: flex;
                    justify-content: center;
                    align-items: center;
                    @media screen and (min-width: $sizeChangeVar) {
                        justify-content: flex-start;
                    }
                    img {
                        height: 337px;
                    }
                }
                .worker {
                    width: 100%;
                    height: 100%;
                    justify-content: center;
                    align-items: flex-end;
                    display: flex;
                    img {
                        height: 400px;
                    }
                    @media screen and (max-width: $sizeChangeVar) {
                        display: none;
                    }
                }
                height: 100%;
                background-color: #00a9d4;
                z-index: -1;
            }
        }
        .dept {
            user-select: none;
            color: #00a9d4;
            margin-top: 25px;
            font-size: 115px;
            font-weight: 300;
            @media screen and (max-width: $sizeChangeVar) {
                font-size: 50px;
                align-items: center;
                justify-content: center;
                justify-items: center;
            }
            width: 100%;
            display: inline-grid;
            align-items: center;
            justify-items: center;
            .text {
                @media screen and (min-width: $sizeChangeVar) {
                    line-height: 150px;
                    width: 825px;
                    display: grid;
                    grid-template-rows: 50% 50%;
                }
                display: flex;
                justify-content: center;
                align-items: center;
                flex-flow: column;
                .animated:before {
                    content: "installe la fibre";
                    animation: topToBottom 10s infinite;
                }

                @keyframes topToBottom {
                    25% {
                        content: "est là pour vous";
                    }
                    50% {
                        content: "réaménage";
                    }
                    75% {
                        content: "bât votre futur";
                    }
                    100% {
                        content: "installe la fibre";
                    }
                }
            }
        }
    }
    .information{
        text-align: center;
        border-left: 2px solid black;
        @media screen and (max-width: $laptopChangeVar){
            border-left: 0px;
        }
        padding: 10px
    }
}
